import db from '@/lib/db';
export default async function updateProductSlugs() {
  try {
    // Get all existing products
    const productsResult = await db.query('SELECT id, name FROM products');
    if (productsResult.rows.length > 0) {
      console.log(`Updating ${productsResult.rows.length} products with slugs...`);
      for (const product of productsResult.rows) {
        // Generate a slug from the product name
        let slug = product.name.toLowerCase().replace(/[^\w\s-]/g, '') // Remove special characters
        .replace(/[\s_-]+/g, '-') // Replace spaces, underscores, and multiple hyphens with single hyphen
        .replace(/^-+|-+$/g, ''); // Remove leading/trailing hyphens

        // Ensure minimum length and handle empty slug
        if (slug.length < 3) {
          slug = `product-${product.id}`;
        }

        // Check for uniqueness and append counter if needed
        let uniqueSlug = slug;
        let counter = 1;
        let slugExists = true;
        while (slugExists) {
          const existingResult = await db.query('SELECT id FROM products WHERE slug = $1 AND id != $2', [uniqueSlug, product.id]);
          if (existingResult.rows.length === 0) {
            slugExists = false;
          } else {
            uniqueSlug = `${slug}-${counter}`;
            counter++;
          }
        }

        // Update the product with the new slug
        await db.query('UPDATE products SET slug = $1 WHERE id = $2', [uniqueSlug, product.id]);
        console.log(`Updated product ${product.id} with slug: ${uniqueSlug}`);
      }
      console.log('All products updated with slugs');
    } else {
      console.log('No products found to update');
    }
  } catch (error) {
    console.error('Error updating product slugs:', error);
  }
}